.video-placeholder {
	position: relative;
	background-color: #111;
	padding-top: 56.25%;
	width: 100%;
	max-width: 100%;
}

.video-placeholder__header {
	font-size: 14px;
	position: absolute;
	top: 50%;
	transform: translateX(-50%);
	left: 50%;
}

.video__views {
	font-size: 18px;
	margin-bottom: 10px;
}

.video__voting {
	font-size: 14px;
}

.video__voting-button {
	color: #636b6f;
	&:hover, &:focus {
		text-decoration: none;
	}
}

.video__voting-button--voted {
	color: #3097D1;
}